<template>
  <v-card elevation="2" outlined>
    <v-card-text>
      <p class="text-h6 text--primary">
        RF Settings
      </p>

      <v-container>
        <v-row>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> RF Max Power </v-col>
          <v-col cols="1" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> Fault </v-col>
          <v-col cols="2"></v-col>

          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Reverse RF Power </v-col>
          <v-col cols="1" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> Fault </v-col>
          <v-col cols="2"></v-col>
        </v-row>

        <v-row>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Inhibit </v-col>
          <v-col cols="1" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> Closed </v-col>
          <v-col cols="3"></v-col>

          <v-col cols="6"></v-col>
        </v-row>

        <v-row>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Bootup RF </v-col>
          <v-col cols="1" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> {{RfOnBootup ? "On" : "Off"}} </v-col>
          <v-col cols="2" align-self="center" >
            <v-btn-toggle color="primary" mandatory v-model="new_RfOnBootup">
              <v-btn outlined :value="true"> 
                On
              </v-btn>
              <v-btn outlined :value="false">
                Off 
              </v-btn>
            </v-btn-toggle>
          </v-col>

          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> RF Offset </v-col>
          <v-col cols="1" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> {{RfOffset}} dB </v-col>
          <v-col cols="2" align-self="center">
            <v-text-field class="text-body-1 font-weight-medium" dense hide-details
              v-model="new_RfOffset"
              type="number" label="RF Offset"></v-text-field>
          </v-col>
          <v-col cols="1"></v-col>
        </v-row>

        <v-row>
          <v-col cols="2" class="text-body-1 d-flex justify-end">
            <v-btn class="mx-1" color="primary"
              :disabled="IsDifferent == false" v-on:click="ChangeRfSettings">Submit </v-btn>
          </v-col>
        </v-row>
      </v-container>
      
    </v-card-text>
  </v-card>
  
</template>


<script>
export default {
  name: 'RfSettings',

  props: {
    RfOnBootup: {
      type: Boolean,
      default: false
    },
    RfOffset: {
      type: Number,
      default: 3.7
    }
  },

  data: function() {
    return {
      new_RfOffset: this.RfOffset,
      new_RfOnBootup: this.RfOnBootup
    }
  },

  computed: {
    IsDifferent() {
      return this.RfOnBootup != this.new_RfOnBootup
        || this.RfOffset != this.new_RfOffset;
    }
  },

  methods: {
    ChangeRfSettings() {
      this.RfOnBootup = this.new_RfOnBootup,
      this.RfOffset = this.new_RfOffset

      var rfSettings = {
        rf_on_startup: this.new_RfOnBootup,
        rf_offset: Number.parseInt(this.new_RfOffset)
      };

      // this.performSetRfSettings(rfSettings);
      console.log(rfSettings);
    },

  },



}
</script>